.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.sidebar {
  margin: 0;
  padding: 0;
  width: 250px;
  background-color: #555;
  position: fixed;
  height: 100%;
  overflow: auto;
  border-right-style: groove;
}

.sidebar a {
  display: block;
  color: white;
  padding: 16px;
  text-decoration: none;
}


.sidebar a:hover:not(.active) {
  background-color: black;
  color: white;
}

.mainContent{
  margin-left: 250px;
  padding: 1px 16px;
  position: relative;
  min-height: 100vh;
  overflow: auto;
}

.noteTab {
  width: 350px;
  margin-left: 250px;
  position: fixed;
  height: 100%;
  overflow: auto;
  border-right-style: groove;
}

div.content {
  margin-left: 600px;
  padding: 1px 16px;
  position: relative;
  min-height: 100vh;
  overflow: auto;
}

div.footer{
  position: fixed;
  margin-left: 600px;
  bottom: 0;
  right: 0;
  left: 0;
  min-width: 56%;
  border-top-style: outset;
}
.scratchPadFooter{
  position: fixed;
  margin-left: 250px;
  bottom: 0;
  right: 0;
  left: 0;
  min-width: 81.5%;
  border-top-style: outset;
}
.iconButton{
  margin-left: .8rem;
  border: none ;
  background: none;
  padding: 0;
}

.spanButton{
  cursor: pointer;
  margin-left: .8rem;
}
.hide {
  display: none;
}

#deleteBtn:hover + .hide {
  display: block;
  color: red;
  text-align: right;
}

@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  .mainContent {margin-left: 0;}
  div.content {margin-left: 0;}
  div.footer {margin-left: 0; position: relative;overflow: auto;}
  .scratchPadFooter{margin-left: 0; position: relative;overflow: auto;}
  .noteTab {
    width: 100%;
    height: auto;
    position: relative;
    margin-left: 0;
  }
}

@media screen and (max-width: 400px) {
  .sidebar a {
    text-align: center;
    float: none;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
